import {
  AppBar,
  Box,
  Button,
  Container,
  createTheme,
  Fab,
  IconButton,
  Stack,
  TextField,
  ThemeProvider,
  Toolbar,
  Typography,
  Avatar,
  Grid,
  Tooltip
} from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import classes from './App.module.css'
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import MicOffIcon from '@mui/icons-material/MicOff';
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import {ScrollTop} from "./components/ScrollTop.tsx";
import SpeechRecognition, {useSpeechRecognition} from "react-speech-recognition";
import {useEffect, useRef, useState} from "react";
import {diffString2, diffString} from "./util/johnresig";
import {diffChars, diffWords, diffWordsWithSpace} from 'diff';

const theme = createTheme({
  components: {
    MuiStack: {
      defaultProps: {
        useFlexGap: true,
      },
    },
  },
});

function App() {
  const {
    transcript,
    listening,
    resetTranscript,
    browserSupportsSpeechRecognition
  } = useSpeechRecognition();

  const [context, setContext] = useState('    \n      .      .            .    .            .       .      50mm .     28,  29   .  ,     ,   31   .        3m  .      .             . .            .      .           . ');
  const [isWrite, setIsWrite] = useState(false);
  const [reducerTranscript, setReducerTranscript] = useState('');
  const [gridCols, setGridCols] = useState(12);
  const [result, setResult] = useState('');
  const refResult = useRef(null);
  const [detectingType, setDetectingType] = useState('char');

  useEffect(() => {
    if(transcript){
      //const _diffString = diffString(transcript, context);
      //console.log(_diffString)
      //setResult(_diffString);

      const _diff = (detectingType === 'word') ? diffWords(transcript, context.replaceAll('.',''), {ignoreCase: true})
        : diffChars(transcript, context, {ignoreCase: true});
      let span = null, fragment = document.createDocumentFragment();

      _diff.forEach((part) => {
        // green for additions, red for deletions
        // grey for common parts
        const color = part.added ? (part.value === '.' || part.value === ',') ? 'green' : 'grey' :
          part.removed ? '' : 'green';

        if(part.removed) return;

        span = document.createElement('span');
        //span.style.color = color;
        span.className = color;
        span.appendChild(document.createTextNode(part.value));

        fragment.appendChild(span);

        /*if(!part.added && !part.removed ){
          console.log(transcript, '< -----------  ')
          console.log(part.value, '< -----------  ')
        }*/
      });

      if(refResult.current){
        refResult.current.innerHTML = '';
        refResult.current.appendChild(fragment);
      }
    }
  }, [transcript, context, refResult]);

  const onChangeTextArea = (event) => {
    setContext(event.target.value);
  }

  const onClickWrite = () => {
    setIsWrite(true)
  }

  const onClickDone = () => {
    setIsWrite(false)
    refResult.current = null;
  }

  const onClickMIC = () => {
    SpeechRecognition.startListening(
      {
        language: 'ko',
        continuous: true
      }
    )
  }

  const onClickMICOff = () => {
    SpeechRecognition.stopListening();
  }

  const onClickReset = () => {
    resetTranscript();
    setResult("");
    if(refResult.current){
      refResult.current = null;
    }
  }

  const onClickHalf = () => {
    setGridCols(exist => exist === 12 ? 6 : 12);
  }

  if (!browserSupportsSpeechRecognition) {
    return <span>Browser doesn't support speech recognition.</span>;
  }

  return(
    <>
      <ThemeProvider theme={theme}>
        <Stack className={classes.total_stack}
               justifyContent="center"
               alignItems="center" spacing={1}>
          <AppBar position="fixed" color="primary"
                  style={{maxWidth: '1280px', minWidth: '720px'}}
                  sx={{ right: 'auto'}}>
            <Toolbar className={classes.header_paper}>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2 }}
              >
                {/*<MenuIcon />*/}
                <Avatar>T</Avatar>
              </IconButton>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                Tele Prompter
              </Typography>
              {
                listening && <span style={{color:'red'}}>recording...</span>
              }
              {
                listening && (
                  <Button color="inherit" onClick={onClickMICOff}>
                    <MicOffIcon />
                  </Button>
                )
              }
              {
                !listening && (<Button color="inherit" onClick={onClickMIC}>
                                <KeyboardVoiceIcon />
                              </Button>)
              }
              <Button color="inherit" onClick={onClickReset}>
                <RestartAltIcon />
              </Button>
              {
                isWrite ? <Button color="inherit" onClick={onClickDone}>Done</Button> :
                  <Tooltip title=" "><Button color="inherit" onClick={onClickWrite}>Write</Button></Tooltip>
              }
              <Tooltip title="  /">
                <Button color="inherit" onClick={onClickHalf} className={classes.detecting_view}>
                  {
                    gridCols === 12 ? <span>View detected Text</span> : <span>Hide detected Text</span>
                  }
                </Button>
              </Tooltip>
              <Tooltip title="   .">
                <Button color="inherit" onClick={() => {setDetectingType(exist => exist === 'word' ? 'char' : 'word')}}>
                  {
                    detectingType === 'word' ? <span>word</span> : <span>char</span>
                  }
                </Button>
              </Tooltip>
            </Toolbar>
          </AppBar>
          <Toolbar id="back-to-top-anchor" />
          <Container className={classes.scroll_body}
                     maxWidth={false}
                     component="main">
            <Grid container spacing={2}>
              <Grid item xs={gridCols}>
                <Box
                  component="form"
                  noValidate
                  autoComplete="off">
                  {
                    isWrite && <TextField
                      id="outlined-multiline-static"
                      multiline
                      fullWidth={true}
                      rows={20}
                      size={'medium'}
                      value={context}
                      onChange={onChangeTextArea}
                    />
                  }
                  {
                    !isWrite && !refResult.current && <div style={{whiteSpace:'pre-line'}} >{context}</div>
                  }
                  {
                    transcript &&
                    !isWrite &&
                    <>
                      <div style={{whiteSpace:'pre-line'}} ref={refResult} className="prompt"/>
                    </>
                  }
                  {
                    /*<br/>
                    <>
                      <div dangerouslySetInnerHTML={{__html: result}}></div>
                    </>*/
                  }
                </Box>
              </Grid>
              <Grid item xs={6} style={gridCols === 12 ? {display: 'none'}: {display: ''}}>
                <Box
                  component="form"
                  noValidate
                  autoComplete="off">
                  {
                    transcript && <div className={classes.detecting_text}>{transcript}</div>
                  }
                </Box>
              </Grid>
            </Grid>
          </Container>
          <ScrollTop>
            <Fab size="small" aria-label="scroll back to top">
              <KeyboardArrowUpIcon />
            </Fab>
          </ScrollTop>
        </Stack>
      </ThemeProvider>
    </>
  );
}

export default App;
